const COCMNSA001P: React.FC<ModalEventProps> = ({ onClose }) => {
    const [ fcNo, setFcNo ] = useState("");
    const [ selectPos, setSelectPos ] = useState({top: 0, left:0, width: 110});

    useEffect(() => {
        const scroll = document.getElementById("fprtRegPp");
        scroll?.addEventListener("scroll", () => {
            console.log(scroll.scrollTop, scroll.clientHeight, scroll.scrollHeight);
        });
    }, []);

    

    const handleClose = () => {
        onClose();
    }
    const selectTelCd = () => {
        const selCount = 7;
        const scrollArea = document.getElementById("fprtRegPp")?.getBoundingClientRect();
        const windowHeight = window.innerHeight;

        const _select = document.getElementsByClassName("select");
        const elem = document.getElementsByClassName("selectboxWrap");
        if (scrollArea && _select && _select.length>0 && elem && elem.length>0) {
            const selList = document.getElementsByClassName("selectList")[0];
            if (selList) {
                if (selList.classList.contains("on")) {
                    selList.classList.remove("on");
                } else {
                    const rect = elem[0].getBoundingClientRect();
                    selList.classList.add("on");
                    const totalHeight = (rect.height) * 7;
                    if ((rect.top + scrollArea.top )+ totalHeight > windowHeight) {
                        setSelectPos({
                            top: rect.top - totalHeight,
                            left: rect.left,
                            width: rect.width
                        })
                    } else {
                        setSelectPos({
                            top: rect.top + rect.height,
                            left: rect.left,
                            width: rect.width
                        })
                    }
                    
                }
            }
        }
    }

    const onFcnoChanged = (event: React.FormEvent<HTMLInputElement>) => {
        const { currentTarget: { value } } = event;
        setFcNo(value);
        console.log("fcNo: " + value);
    }

    // VAC Pattern 적용
    // 퍼블리셔와 웹개발자간의 협업에 유리
    const props = {
        onClose: handleClose,
        selectTelCd: selectTelCd,
        onFcNoChange: onFcnoChanged,
        fcNo: fcNo,
        selectPos: selectPos
    }
    return <COCMNSA001PView {...props} />
}

